<template>
  <div class="sign">
    <div class="sign-logo">
      <img src="../assets/logo.png" alt="">
    </div>
      <div class="sign-title">
        <h3>東明養護中心臥床偵測系統</h3>
      </div>
      <div class="sign-action">
        <Row>
          <Col span="18" offset="3">
          <Card dis-hover
                :bordered="false">
            <div>
              <Input prefix="ios-contact"
                     placeholder="请输入账号"
                     size="large"
                     v-model="userName"
                     style="width: 300px; margin-top: 20px;" />
            </div>
              <div style="margin-top: 6px">
                <Input prefix="ios-lock"
                       placeholder="请输入密码"
                       size="large"
                       v-model="pwd"
                       type="password"
                       style="width: 300px;margin-top: 10px;" />
            </div>
                <Button type="success"
                        :loading="isLoading"
                        :disabled="!userName || !pwd"
                        @click.native="onLogIn">登录</Button>
          </Card>
          </Col>
        </Row>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
import md5 from 'js-md5'

export default {
  data () {
    return {
      userName: undefined,
      pwd: undefined,
      isLoading: false
    }
  },
  methods: {
    onLogIn () {
      this.isLoading = true
      axios({
        method: 'post',
        url: '/api/login',
        params: {
          userName: this.userName,
          pwd: md5(this.pwd)
        }
      })
        .then(() => {
          this.isLoading = false
          window.localStorage.setItem('isToken', 1)
          this.$Message.success('登录成功，正在跳转...')
        })
        .catch(() => {
          this.isLoading = false
          this.$Message.error('账号或密码错误，请重试')
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.sign {
  &-logo {
    width: 300px;
    margin: 0 auto;

    img {
      width: 100%;
    }
  }

  &-title {
    margin-top: 30px;
    font-size: 32px;
  }

  .ivu-btn {
    width: 120px;
    margin-top: 40px;
  }
}
</style>



