<template>
  <div class="list">
    <Modal v-model="addModal"
           title="新增床位"
           @on-ok="onSaveAdd">
      <div class="list-modal-item">
        <span>床位编号</span>
        <Input placeholder="请输入床位编号"
               v-model="models.bedNum"
               size="large" />
      </div>
      <div class="list-modal-item">
        <span>病患名称</span>
        <Input placeholder="请输入病患名称"
               v-model="models.personName"
               size="large" />
      </div>
      <div class="list-modal-item">
        <span>设备Mac</span>
        <Input placeholder="请输入设备Mac"
               v-model="models.fitSleepMac"
               size="large" />
      </div>
    </Modal>

    <Modal v-model="editModal"
           title="编辑床位"
           @on-ok="onSaveEdit">
      <div class="list-modal-item">
        <span>床位编号</span>
        <Input placeholder="请输入床位编号"
               v-model="editModels.bedNum"
               size="large" />
      </div>
      <div class="list-modal-item">
        <span>病患名称</span>
        <Input placeholder="请输入病患名称"
               v-model="editModels.personName"
               size="large" />
      </div>
      <div class="list-modal-item">
        <span>设备Mac</span>
        <Input placeholder="请输入设备Mac"
               v-model="editModels.fitSleepMac"
               size="large" />
      </div>
    </Modal>
    <div class="list-menu">
      <Menu :theme="theme"
            active-name="2">
        <div class="logo">
          <img src="../assets/logo.png"
               alt="">
        </div>
        <MenuGroup title="睡眠侦测">
          <MenuItem name="1"
                    @click.native="$router.push({ name: 'List' })">
          <Icon type="md-document" />
          即时状态
          </MenuItem>
          <MenuItem name="2">
          <Icon type="md-chatbubbles" />
          系统设置
          </MenuItem>
        </MenuGroup>
      </Menu>
    </div>
    <div class="list-content">
      <div class="list-content-action">
        <Button type="success"
                @click.native="onAdd"
                icon="md-add">新增</Button>
      </div>
      <Table border
             :columns="columns"
             :data="bedList"></Table>
    </div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      addModal: false,
      editModal: false,
      theme: 'light',
      bedList: [],
      models: {
        bedNum: undefined,
        personName: undefined,
        fitSleepMac: undefined
      },
      editModels: {
        bedNum: undefined,
        personName: undefined,
        fitSleepMac: undefined
      },
      columns: [
        {
          title: '床位编号',
          key: 'bedNum'
        },
        {
          title: '病号名称',
          key: 'personName'
        },
        {
          title: '设备Mac',
          key: 'fitSleepMac'
        },
        {
          title: '电量',
          key: 'power'
        },
        {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.onEdit(params.index)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.onRemove(params.index)
                  }
                }
              }, '删除')
            ])
          }
        }
      ]
    }
  },
  methods: {
    remove (index) {
      this.data6.splice(index, 1)
    },
    onAdd () {
      this.models.bedNum = undefined
      this.models.personName = undefined
      this.models.fitSleepMac = undefined

      this.addModal = true
    },
    onSaveAdd () {
      axios({
        method: 'post',
        url: '/api/addBed',
        params: {
          ...this.models
        }
      })
        .then(() => {
          this.$Message.success('床位新增成功')
        })
        .catch(() => {
          this.$Message.error('床位新增失败')
        })
    },
    onSaveEdit () {
      axios({
        method: 'post',
        url: '/api/updateBed',
        params: {
          ...this.editModels
        }
      })
        .then(() => {
          this.$Message.success('床位编辑成功')
        })
        .catch(() => {
          this.$Message.error('床位编辑失败')
        })
    },
    getBedList () {
      axios({
        method: 'get',
        url: '/api/getBedList'
      })
        .then(res => {
          this.bedList = res.response.data
        })
        .catch(() => {
          this.$Message.error('获取床位列表失败，请稍后重试...')
        })
    },
    onEdit (index) {
      this.editModels = this.bedList[index]
      this.editModal = true
    },
    onRemove (index) {
      axios({
        method: 'get',
        url: '/api/delBed',
        params: {
          id: this.bedList[index].id
        }
      })
        .then(res => {
          this.$Message.error('删除床位成功')
          this.getBedList()
        })
        .catch(() => {
          this.$Message.error('删除床位失败，请稍后重试...')
        })
    }
  },
  mounted () {
    this.getBedList()
  }
}
</script>
<style lang="scss" scoped>
.list {
  height: 100vh;
  display: flex;

  .ivu-menu {
    height: 100%;
  }

  .logo {
    width: 100px;
    margin-left: 70px;

    img {
      width: 100%;
    }
  }

  &-content {
    width: calc(100vw - 240px);
    height: 100vh;
    overflow: auto;

    .ivu-table-wrapper {
      width: calc(100vw - 400px);
      margin-left: 80px;
      margin-top: 20px;
    }

    &-action {
      width: calc(100vw - 400px);
      margin-left: 80px;
      margin-top: 20px;
      display: flex;
      justify-content: flex-end;
    }
  }
}

.list-modal {
  &-item {
    margin-bottom: 20px;
    .ivu-input-wrapper {
      margin-left: 30px;
      width: 300px;
    }
  }
}
</style>



