import Vue from 'vue'
import Router from 'vue-router'
import SignIn from '@/components/signIn'
import List from '@/components/list'
import Setting from '@/components/setting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'List',
      component: List
    },
    {
      path: '/signIn',
      name: 'SignIn',
      component: SignIn
    },
    {
      path: '/setting',
      name: 'Setting',
      component: Setting
    }
  ]
})



