<template>
  <div class="list">
    <div class="list-menu">
      <Menu :theme="theme3"
            active-name="1">
        <div class="logo">
          <img src="../assets/logo.png" alt="">
      </div>
          <MenuGroup title="睡眠侦测">
            <MenuItem name="1">
            <Icon type="md-document" />
            即时状态
            </MenuItem>
            <MenuItem name="2"
                      @click.native="$router.push({ name: 'Setting' })">
            <Icon type="md-chatbubbles" />
            系统设置
            </MenuItem>
          </MenuGroup>
      </Menu>
    </div>

    <div class="list-content">
      <div class="list-content-tip">
        <div class="list-content-tip-item">
          <Icon type="ios-sunny-outline"
                size="30" />
          <p>监控中</p>
        </div>
        <div class="list-content-tip-item">
          <Icon type="ios-rainy-outline"
                size="30" />
          <p>翻身提醒</p>
        </div>
        <div class="list-content-tip-item">
          <Icon type="ios-thunderstorm-outline"
                size="30" />
          <p>离床提醒</p>
        </div>
        <div class="list-content-tip-item">
          <Icon type="ios-snow-outline"
                size="30" />
          <p>离线</p>
        </div>
        <div class="list-content-tip-item">
          <Icon type="ios-heart-outline"
                size="30" />
          <p>心跳</p>
        </div>
        <div class="list-content-tip-item">
          <Icon type="ios-cloud-outline"
                size="30" />
          <p>呼吸</p>
        </div>
      </div>
      <div class="list-content-list">
        <div class="list-content-item"
             v-for="(bed, index) in bedList"
             :key="index">
          <p>{{ bed.bedNum }}</p>
          <Icon :type="workstatus | icon"
                size="30" />
          <span class="heart"
                v-if="bed.heartRate">
            <Icon type="ios-heart-outline" />{{ bed.heartRate }}</span>
          <span class="breath"
                v-if="bed.breathRate">
            <Icon type="ios-cloud-outline" />{{ bed.breathRate }}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      theme3: 'light',
      bedList: []
    }
  },
  methods: {
    getBedStatus () {
      axios({
        method: 'get',
        url: '/api/getBedStatusList'
      })
        .then(res => {
          this.bedList = res.response.data
        })
        .catch(() => {
          this.$Message.error('获取床位状态失败，请稍后重试...')
        })
    }
  },
  filters: {
    icon (icon) {
      switch (icon) {
        case '0':
          return 'ios-sunny-outline'
        case '1':
          return 'ios-rainy-outline'
        case '2':
          return 'ios-thunderstorm-outline'
        case '3':
          return 'ios-snow-outline'
      }
    }
  },
  mounted () {
    this.getBedStatus()
  }
}
</script>
<style lang="scss" scoped>
.list {
  height: 100vh;
  display: flex;

  .ivu-menu {
    height: 100%;
  }

  .logo {
    width: 100px;
    margin-left: 70px;

    img {
      width: 100%;
    }
  }

  &-content {
    width: calc(100vw - 240px);
    height: 100vh;
    overflow: auto;

    &-list {
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      height: 80vh;
      margin-top: 20px;
      justify-content: space-evenly;
    }

    &-item {
      flex: 0 0 18%;
      height: 180px;
      border: 1px solid #f0f0f0;
      border-radius: 5px;
      box-shadow: 2px 2px 2px rgba($color: #eee, $alpha: 0.3);
      position: relative;

      .ivu-icon {
        margin-top: 40px;
      }

      p {
        font-size: 16px;
        font-weight: 700;
        margin-top: 20px;
      }

      span {
        position: absolute;

        .ivu-icon {
          margin-top: 0;
          margin-right: 5px;
        }

        &.breath {
          top: 10px;
          right: 10px;
          color: #013c80;
        }

        &.heart {
          top: 10px;
          left: 10px;
          color: #cd0a20;
        }
      }
    }

    &-tip {
      display: flex;
      justify-content: space-around;
      margin-top: 20px;
    }
  }
}
</style>



